<template>
  <button
      @click="action"
      :class="[styleButton, { disabled: disabled }]"
      :style="{width: width}"
      :disabled="disabled"
  >
    <slot></slot>
  </button>
</template>

<script>
export default {
  name: "c-button",
  props: {
    mode: {
      type: String,
      default: 'primary'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    width: {
      type: String,
      default: '100%'
    }
  },
  data() {
    return {}
  },
  computed: {
    styleButton() {
      if (this.mode === 'primary') return 'primary'
      if (this.mode === 'secondary') return 'secondary'
      if (this.mode === 'none') return 'none'
    }
  },
  methods: {
    action() {
      this.$emit('click')
    }
  }
}
</script>

<style lang="scss" scoped>


.primary {
  all: unset;
  height: 52px;
  //TODO: Global Var
  background-color: #950C50;
  border-radius: 0.5rem;
  font-weight: 700;
  text-align: center;
  color: white;
  cursor: pointer;
}

.secondary {
  all: unset;
  color: #950C50;
  font-weight: 700;
  cursor: pointer;
  height: 52px;
  border: 2px solid #950C50;
  border-radius: 0.5rem;
  text-align: center;
}

.none {
  all: unset;
  color: white;
  font-weight: 500;
  cursor: pointer;
}

.disabled {
  opacity: 20%;

  :hover {
    all: unset;
  }
}

</style>