<template>
  <div id="Refuse">
    <div class="title">
      دلیل انصراف از درخواست
    </div>
    <div class="options">
      <template v-for="(item, index) in cancelationOptions">
        <c-radio-box class="radioBoxItem" :label="item.title" :value="item.id" v-model="selectedValue"/>
      </template>
    </div>
    <div class="confirm">
      <c-button @click="confirm" :mode="btnMode" :disabled="btnDisabled">ثبت</c-button>
    </div>
  </div>
</template>

<script>
import TheFooter from "@/components/TheFooter";
import CInput from "@/components/c-input";
import CButton from "@/components/c-button";
import CTextarea from "@/components/c-textarea";
import CRadioBox from "@/components/c-radio-box";
import Service from "@/providers/service";

export default {
  name: "Refuse",
  components: {
    CRadioBox,
    CTextarea,
    CButton,
    CInput,
    TheFooter,
  },
  data() {
    return {
      selectedValue: "",
      mobile: "۰۹۹۰۳۵۲۶۸۹۴",
      name: "سید محمدرضا حسینی فرد",
      cancelationOptions: [],
      customerId: 0,
      subCategoryId: 0,
      // name: "محمد مصطفی تربتی راد",
      // address: "تهران، بهارستان",
      // address: "قم، خیابان آذر، کوچه 29، پلاک 42",
      address: "خیابان آذر، خیابان آذر، خیابان آذر، خیابان آذر، خیابان آذر، خیابان آذر، خیابان آذر، خیابان آذر، خیابان آذر، ",
    }
  },
  mounted() {
    // if (!this.customerId) this.$router.go(-1)
    this.cancelationOptions = this.$route.params.cancelationOptions
    this.customerId = this.$route.params.customerId
    this.subCategoryId = this.$route.params.subCategoryId
  },
  computed: {
    btnDisabled() {
      return !this.selectedValue
    },
    btnMode() {
      if (this.selectedValue == "") return 'secondary'
      if (this.selectedValue != "") return 'primary'
    }
  },
  methods: {
    async confirm() {
      try {
        let response = await Service.refuseRequest({
          customerId: this.customerId,
          subCategoryId: this.subCategoryId,
          reasonId: this.selectedValue,
        })
        if (response?.status == 200) this.$router.push('/refuseend')
      } catch (e) {
        console.log(e)
      }
    },
  }
}
</script>

