import ApiService from "@/providers/api-service";

const Service = {
    async getAddressCustomer({mobile, subCategoryId}) {
        ApiService.init()
        return await ApiService.get(process.env.VUE_APP_API_URL + `Customers/GetAddressRetLanding`, {
            params: {
                mobile,
                subCategoryId,
            }
        })
    },
    async repairmanRequest(body) {
        ApiService.init()
        try {
            // let body = {
            //     "mobile": "9193578895",
            //     "subCategoryId": 249,
            //     "addressId":null,
            //     "newFullAddress": "ادرس جدید اگه کد ادرس نداشته باشیم به این نگاه میکنم",
            // }
            let response = await ApiService.post(process.env.VUE_APP_API_URL + `Requisites/landing`, body);
            console.log('response', response)
            return response
        } catch (e) {
            console.log(e)
        }
    },
    async refuseRequest(body) {
        ApiService.init()
        // let body = {
        //     "customerId": 8340,
        //     "subCategoryId": 249,
        //     "reasonId": 1
        // }
        try {
            return await ApiService.post(process.env.VUE_APP_API_URL + `GiveUpLeads/`, body);
        } catch (e) {
            console.log(e)
        }

    },
}

export default Service