<template>
  <div id="home">
    <div class="input">
      <c-input v-model="name" label="نام و نام خانوادگی"/>
    </div>
    <div class="input">
      <c-input v-model="mobile" label="شماره همراه" inputType="mobile"/>
    </div>
    <div class="input">
      <!--      <c-textarea ref="c-textarea" v-model="selectedAddress" label="آدرس" :disabled="disabledTextArea"/>-->
      <c-textarea ref="c-textarea" v-model="selectedAddress" label="آدرس" :disabled="disabledTextArea"/>
    </div>
    <div class="changeAddress">
      <c-button @click="changeAddressBtn" :mode="'none'">
        تغییر یا اضافه کردن آدرس جدید
      </c-button>
    </div>
    <div class="action">
      <c-button @click="AcceptRequest" :mode="'primary'" width="70%">
        من درخواست تعمیرکار پکیج دارم
      </c-button>
      <c-button @click="refusePage" class="reqbtn" :mode="'none'" width="30%">
        نه نمیخوام
      </c-button>
    </div>

    <vue-bottom-sheet ref="myBottomSheet" class="vue-bottom-sheet">
      <div class="bottomSheet">
        <div class="bottomSheetAction">
          <span class="newAddressTitle">انتخاب آدرس</span>
        </div>
        <div class="radioBox">
          <template v-for="(item, index) in addresses">
            <c-radio-box @change="changeAddress" class="radioBoxItem" :label="item.FullAddress" :value="item.Id"
                         v-model="selectedAddressId"/>
          </template>
        </div>
        <div class="containerNewAddress">
          <c-button class="newAddressBtn" :mode="'secondary'">
            <div @click="selectNewAddress" class="newAddress">
              <div class="newAddressImg"><img src="@/assets/Plus.svg" alt=""></div>
              <div>افزودن آدرس جدید</div>
            </div>
          </c-button>
        </div>
      </div>
    </vue-bottom-sheet>

  </div>
</template>

<script>
import Service from "@/providers/service";
import TheFooter from "@/components/TheFooter";
import CInput from "@/components/c-input";
import CButton from "@/components/c-button";
import CTextarea from "@/components/c-textarea";
import VueBottomSheet from "@webzlodimir/vue-bottom-sheet";
import CRadioBox from "@/components/c-radio-box";

export default {
  name: "Home",
  components: {
    CRadioBox,
    CTextarea,
    CButton,
    CInput,
    VueBottomSheet,
    TheFooter,
  },
  data() {
    return {
      data: {},
      disabledTextArea: true,
      mobile: "",
      subCategoryId: 0,
      selectedAddressId: 0,
      selectedAddress: null,
      addresses: [],
      cancelationOptions: []
    }
  },
  async mounted() {
    this.mobile = this.$route.query['m']
    this.subCategoryId = this.$route.query['sb']
    try {
      let {data} = await Service.getAddressCustomer({
        mobile: this.mobile,
        subCategoryId: this.subCategoryId,
      })
      console.log('dataa', data)
      this.data = data.res
      this.addresses = this.data.addresses
      this.selectedAddressId = this.addresses[0].Id
      this.selectedAddress = this.addresses[0].FullAddress
      this.cancelationOptions = this.data.cancelationOptions
      if (!this.cancelationOptions.length) this.$router.push('/refuseend')
    } catch (data) {
      if (data.response.data.error.isRequested) this.$router.push('/theend')
      // if (data.response.data.error.isRequested) {
      //   this.$router.push('/theend')
      // }
      // console.log('Error Data', data.response.data.error.isRequested)
    }

  },
  computed: {
    name() {
      return !!Object.keys(this.data).length ? `${this.data.firstName + ' ' + this.data.lastName}` : "";
    },
    customerId() {
      return this.addresses[0].customerId
    }
  },
  methods: {
    selectNewAddress() {
      console.log('selectNewAddress')
      this.$refs.myBottomSheet.close()
      this.$refs["c-textarea"].resize()
      this.disabledTextArea = false
      this.selectedAddressId = null
      this.selectedAddress = ""
      setTimeout(() => {
        this.$refs["c-textarea"].resize()
        this.$refs["c-textarea"].focus()
      }, 100)
    },
    AcceptRequest() {
        Service.repairmanRequest(
            {
              mobile: this.mobile,
              subCategoryId: this.subCategoryId,
              addressId: this.disabledTextArea ? this.selectedAddressId : null,
              newFullAddress: this.selectedAddress,
            }
        )
      this.$router.push('/theend');
    },
    refusePage() {
      // setTimeout(() => {
      this.$router.push({name: 'Refuse', params: {
          cancelationOptions: this.cancelationOptions,
          subCategoryId: this.subCategoryId,
          customerId: this.customerId,
        }});
      // }, 0)
    },
    changeAddress(event) {
      console.log('changeAddress')
      this.$refs.myBottomSheet.close()
      this.addresses?.forEach((item) => {
        if (item.Id == event) {
          this.selectedAddressId = item.Id
          this.selectedAddress = item.FullAddress
        }
      })
      this.disabledTextArea = true
      setTimeout(() => {
        this.$refs["c-textarea"].resize()
      }, 100)
    },
    changeAddressBtn() {
      this.$refs.myBottomSheet.open()
    }
  }
}
</script>

<style lang="scss" scoped>
.containerNewAddress {
  position: fixed;
  bottom: 0;
  background-color: white;
  width: 90%;
}

.vue-bottom-sheet {
  display: flex;
  padding: 2rem;
}

.radioBoxItem {
  margin-bottom: 32px;
}

.newAddressTitle {
  font-weight: 700;
}

.radioBox {
  direction: rtl;
}

.radioBox input[type=radio] {
  display: none;
}

.radioBox > input[type=radio] {
  display: none;
}

.newAddressImg {
  display: flex;
  margin-left: 0.5rem;
}

.newAddressBtn {
  margin: 1rem 0;
}

.newAddress {
  display: flex;
  justify-content: center;
  align-items: center;
}

.bottomSheet {
  direction: rtl;
  margin: 0 1rem;
  margin-bottom: 7rem;
}

.bottomSheetAction {
  font-weight: 700;
  margin-bottom: 24px;
}

.action {
  display: flex;
  align-items: center;
  justify-content: space-between;
  direction: rtl;

  .reqbtn {
    text-align: center;
    font-size: 16px;
    margin-right: 0.5rem;
  }
}

.changeAddress {
  direction: rtl;
  color: white;
  margin-bottom: 4.5rem;
  font-size: 14px;
}

#home {
  margin-top: 62px;

  .input {
    margin-bottom: 1rem;
  }
}

</style>