import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/Home.vue'
import Welcome from '../views/Welcome.vue'
import Refuse from '../views/Refuse.vue'
import TheEnd from '../views/TheEnd.vue'
import RefuseEnd from '../views/RefuseEnd.vue'
import Temp from '../views/Temp.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    redirect: '/home'
  },
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView
  },
  {
    path: '/welcome',
    name: 'Welcome',
    component: Welcome
  },
  {
    path: '/theend',
    name: 'TheEnd',
    component: TheEnd
  },
  {
    path: '/refuseEnd',
    name: 'RefuseEnd',
    component: RefuseEnd
  },
  {
    path: '/refuse',
    name: 'Refuse',
    component: Refuse
  },
  {
    path: '/temp',
    name: 'Temp',
    component: Temp
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
