<template>
  <div class="inputClass">
    <div class="inputLabel"> {{ label }}</div>
    <div class="input">
      <input class="innerInput" :name="name" :value="inputValue" @input="onInput" @change="onChange" type="text" disabled/>
    </div>
  </div>
</template>

<script>
export default {
  name: "c-input",
  props: ['label', 'value', 'inputType'],
  // props: {
  //   label: {
  //     type: String,
  //     required: true,
  //   },
  //   value: {
  //     type: String || Number,
  //     required: false,
  //   },
  // },
  computed: {
    name() {
      // return this.label.toLowerCase();
    },
    inputValue() {
      if (this.inputType === 'mobile') return '0' + this.value
      return this.value
    }
  },
  mounted() {
  },

  methods: {
    onInput(event) {
      if (this.inputType === 'mobile') {
        console.log('onInput inputType', event.target.value.slice(1))
        this.$emit('value', event.target.value.slice(1));
        return
      }
      this.$emit('value', event.target.value);
    },
    onChange(event) { // Supports .lazy
      // if (this.inputType === 'mobile') {
      //   console.log('onChange inputType', event.target.value.slice(1))
      //   this.$emit('value', event.target.value.slice(1));
      //   return
      // }
      this.$emit('change', event.target.value);
    },
  }
}
</script>

<style lang="scss" scoped>

.inputClass {
  direction: rtl;
  background-color: white;
  border-radius: 0.5rem;
  height: 52px;
  display: flex;
  align-items: center;
  padding: 0 0.75rem;
  font-weight: 700;

  .input {
    width: 60%;
  }

  .innerInput {
    all: unset;
    width: 100%;
  }
}

//TODO: Fix Nested
//TODO: Fix Global Variable
.inputLabel {
  width: auto;
  color: #999999;
  padding-left: 0.75rem;
  font-weight: 500;
}

