<template>
  <div class="inputClass">
    <div class="inputLabel">
      {{ label }}
    </div>
    <template>
      <textarea :value="value" class="textarea" @input="input($event)" ref="textarea" :autofocus="true" :disabled="disabled"></textarea>
    </template>
  </div>
</template>

<script>

export default {
  name: "c-textarea",
  props: {
    label: {
      type: String,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false
    },
    value: {
      type: String || Number,
      required: false,
    },
  },
  data() {
    return {
      textAreaID: 1,
      autofocus: false
    }
  },
  mounted() {
    // this.$nextTick(() => {
    //     this.resize()
    // })
    setTimeout(() => {
      this.resize()
    }, 100)
  },
  methods: {
    resize() {
      // setTimeout(() => {
      //   console.log('c-textarea methods resize')
        let element = this.$refs["textarea"];
        element.style.height = "18px";
        element.style.height = element.scrollHeight + "px";
      // }, 100)
    },
    input(e) {
      this.$emit('input', e.target.value);
      // setTimeout(() => {
        this.resize()
      // }, 100)
      // console.log('event', e)
    },
    focus() {
      this.autofocus = true
    }
  }
}
</script>

<style lang="scss" scoped>

.inputClass {
  direction: rtl;
  background-color: white;
  border-radius: 0.5rem;
  display: flex;
  align-items: flex-start;
  padding: 0.75rem 0.75rem;
  font-weight: 700;

  input {
    all: unset;
  }
}

//TODO: Fix Nested
//TODO: Fix Global Variable
.inputLabel {
  color: #999999;
  padding-left: 0.75rem;
  font-weight: 500;
}

.textarea {
  all: unset;
  word-wrap: break-word;
  color: black;
  width: 300px;
  height: 29px;
  padding: 2px;
  resize: none;
  overflow: hidden;
  background-color: transparent;
  border: none;
  font-size: 14px;
  font-weight: 700;
}

.textarea:focus {
  outline: none;
}

</style>